/*global chrome*/
import React from "react";
const Add = ({ cb }) => {
  return (
    <div className="Button__Wrapper">
      <button
        className="Button__Self"
        onClick={() =>
          chrome.tabs.query({ active: true, currentWindow: true }, function(tabs) {
            chrome.storage.sync.get("list", items => {
              const thisPage = { id: items.list.length, isRead: false, title: tabs[0].title, url: tabs[0].url };
              if (items.list.every(c => c.url != thisPage.url)) chrome.storage.sync.set({ list: [...items.list, thisPage] }, () => cb([...items.list, thisPage]));
            });
          })
        }
      >
        Add
      </button>
    </div>
  );
};

export default Add;
